@import "./fonts/stylesheet.css";
@import url('https://cdn.jsdelivr.net/npm/bootswatch@5.1.0/dist/quartz/bootstrap.min.css');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans-Italik", sans-serif;
}
body{
  background: url("https://images.pexels.com/photos/326311/pexels-photo-326311.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
}
.todo-container{
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
  border: 1px solid #000;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  padding: 20px;
  position: absolute;
  .todo-header{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 15px;
  }
  .search{
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    &-input{
      width: 400px;
      outline: none;
    }
    .buttons{
      display: flex;
      justify-content: space-between;
    }
  }
  .todo-list{
    margin-bottom: 15px;
    .todo-item{
      &__name{
        &.done{
          text-decoration: line-through;
        }
        &.important{
          text-transform: uppercase;
          color: #fff811;
        }
      }
    }
  }
  .add-form{
    &-input{
      width: 400px;
    }
  }
}
